/**
 * @typedef {object} TiledElement
 * @property {string} vueElement
 * @property {object} props
 * @property {number} props.width
 * @property {number} props.height
 */

/**
 * @typedef {object} TiledLine
 * @property {Array<TiledElement>} elements
 * @property {number} height
 */
export default {
  name: 'TiledLayout',
  props: {
    elements: {
      type: Array,
      required: true
    }
  },
  computed: {
    /**
     * @return {number} The width of the elements container
     */
    containerWidth: function containerWidth() {
      return this.$refs.container.clientWidth;
    },

    /**
     * @return {Array<TiledLine>}
     */
    layout: function layout() {
      /** @type {Array<TiledElement>} */
      var elements = this.element.slice(0);
      var layout = [];
      var lineNumber = 0;

      while (elements.length > 0) {
        var currentLine = {
          elements: [],
          height: 0
        };
        var lineWidth = 0;

        while (lineWidth < this.containerWidth && elements.length > 0) {
          currentLine.push(elements.shift());
          lineWidth = currentLine.map(function (element) {
            return element.props.width;
          }).reduce(function (sum, elementRatio) {
            return sum + elementRatio;
          });
        }

        elements.unshift(currentLine.pop());
        var sumOfRatio = currentLine.map(function (element) {
          return element.props.width / element.props.height;
        }).reduce(function (sum, elementRatio) {
          return sum + elementRatio;
        });
        currentLine.height = this.containerWidth / sumOfRatio;
        layout[lineNumber++] = currentLine;
      }

      return layout;
    }
  },
  methods: {}
};